import React, { Component } from "react";
import SimpleStorageContract from "./contracts/SimpleStorage.json";
import getWeb3 from "./getWeb3";

import "./App.css";
import Navbar from "./Navbar";
import Roadmap from "./Roadmap";
import About from "./About";
import Team from "./Team";
import Footer from "./Footer";
import Video from "./Video";

class App extends Component {
  constructor(props) {
    super(props);
    this.refRoadmap = React.createRef();
    this.refAbout = React.createRef();
    this.refTeam = React.createRef();

    this.state = { connected: false, web3: null, accounts: null };
  }

  componentDidMount = async () => {
    this.connectWallet();
  };

  connectWallet = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();
      console.log(this.state.web3);
      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      const deployedNetwork = SimpleStorageContract.networks[networkId];
      this.instance = new web3.eth.Contract(
        SimpleStorageContract.abi,
        deployedNetwork && deployedNetwork.address
      );
      console.log(this.instance);

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ connected: true, accounts: accounts, web3: web3 });
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(`Failed to Connect with Your Wallet`);
      console.error(error);
    }
  };

  mintNft = () => {
    console.log(this.state.connected);
  };

  roadmapScroll = () => {
    this.refRoadmap.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
    });
  };

  aboutScroll = () => {
    this.refAbout.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
    });
  };
  teamScroll = () => {
    this.refTeam.current.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
    });
  };

  render() {
    return (
      <div className="main-app">
        <div className="cover-app d-flex h-100 text-center text-white">
          <div className="cover-container d-flex w-100 h-100 p-3 mx-auto flex-column">
            <Navbar
              roadmap={this.roadmapScroll}
              about={this.aboutScroll}
              team={this.teamScroll}
            />

            <main className="px-3">
              <h1 className="main-heading cover-header">
                Welcome to the Catoodle Family
              </h1>
              <p className="lead cover-sub">
              7.777 Frens ready to conquer the metaverse First 1500 Free
              </p>
              <p className="lead">
                {this.state.connected == false ? (
                  <form>
                    <button
                      className="btn btn-lg btn-secondary fw-bold border-white bg-white cover-button"
                      onClick={this.connectWallet}
                    >
                      Connect Wallet
                    </button>
                  </form>
                ) : (
                  <button
                    className="btn btn-lg btn-secondary fw-bold border-white bg-white cover-button"
                    onClick={this.mintNft}
                  >
                    Mint!
                  </button>
                )}
              </p>
            </main>
          </div>
        </div>

        <Video />
        <span ref={this.refRoadmap}>
          <Roadmap />
        </span>
        <span ref={this.refAbout}>
          <About />
        </span>
        <span ref={this.refTeam}>
          <Team />
        </span>

        <Footer />
      </div>
    );
  }
}

export default App;
